"use strict";

/**
 * Bootstrap Class
 */
var Bootstrap = {

  /**
   * Initializer
   */
  init: function init() {

    this.startPopover();
    this.startTooltip();
  },

  /**
   * 
   */
  startPopover: function startPopover() {

    $("a[rel~=popover], .has-popover").popover();
  },

  startTooltip: function startTooltip() {

    $("a[rel~=tooltip], .has-tooltip").tooltip();
  }

};

module.exports = Bootstrap;
