"use strict";

/**
 * Product Class
 */
var Product = {

  /**
   * Like a product
   */
  like: function like(productId, callback) {

    $.ajax({
      method: "PUT",
      url: "/api/customer/favorites/" + productId,
      data: {}

    }).done(function (res) {

      callback(res);
    }).error(function (err) {

      callback({ success: false, error: err.responseJSON.error });
    });
  },

  /**
   * Unlike a product
   */
  unlike: function unlike(productId, callback) {

    $.ajax({
      method: "DELETE",
      url: "/api/customer/favorites/" + productId,
      data: {}

    }).done(function (res) {

      callback(res);
    }).error(function (err) {

      callback({ success: false, error: err.responseJSON.error });
    });
  }

};

module.exports = Product;
