'use strict';

/**
 * OrdersShow class
 */
var OrdersShow = {

  /**
   * Initializer
   */
  init: function init() {

    this.multiSelectSystem();
  },

  multiSelectSystem: function multiSelectSystem() {

    $('select.sku-variants-options').multiselect({
      enableCaseInsensitiveFiltering: true,
      maxHeight: 400
    }).multiselect('disable');
  }

};

module.exports = OrdersShow;
