"use strict";

/**
 * LazyLoader Class
 */
var LazyLoader = {

  /**
   * Initializer
   */
  init: function init() {

    this.startLazyLoader();
  },

  /**
   *
   */
  startLazyLoader: function startLazyLoader() {

    $("div.lazy").lazyload({
      effect: "fadeIn"
    });
  }

};

module.exports = LazyLoader;
